<template>
    <div>
        <div id='tempheader'>
            <!-- <img :src="require('../assets/logo.png')" alt='Logo' width=209 height=125/> -->
            <img :src="require('../assets/logo2.png')" alt='Logo' width=240 height=106/>
            <div id="descriptor">
                <h1>ДОСТАВКА ПИЦЦЫ. МОСКВА</h1>
            </div>
            <div id="phone">
                <h2>+7 (495) 792-17-00</h2>
            </div>            
        </div>
        
        <div id="header">
            <div id="header-menu">
                <app-header-link url="/main" text="МЕНЮ" @click.native='filterByCategory(false)' />                
                <app-header-link v-for='categ in getCategories' :key='categ.id' :url='"/catalog" + categ.url' :text="categ.text | uppercase" @click.native='filterByCategory(categ.id)' />            
            </div>            
            <div id="cart-link" v-if='this.$store.state.ordered.length > 0'>                
                <app-header-link url="/order" :text="`В корзине ${getOrderItemsCount} шт. на ${getOrderSum} ₽`" />                
            </div>
        </div>
    </div>
</template>

<script>
    import HeaderLink from "./HeaderLink";
    import { mapGetters, mapMutations } from 'vuex';
    export default {

        components: {
            'app-header-link': HeaderLink,
        },

        created() {            
            window.addEventListener('scroll', this.handleScroll);
        },

        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },

        methods: {
            ...mapMutations([
                'filterByCategory',
            ]),
            handleScroll(event) {                        
                const scrolled = window.pageYOffset 
                    || document.documentElement.scrollTop;
                if (scrolled > 200) return;
                const tempheader = document.getElementById('tempheader');
                const menu = document.getElementById('header');
                
                let tempheaderTop = -scrolled;
                let menuTop = 150 - scrolled;
                if (menuTop < 0) { menuTop = 0 };
                tempheader.style.top = tempheaderTop + 'px';
                menu.style.top = menuTop + 'px';
            }
        },

        computed: {
            ...mapGetters([
                'getOrderItemsCount',
                'getOrderSum',
                'getCategories',
            ])
        }
    }
</script>

<style scoped>

    h1 {
        font-size: 1.5em;
    }

    h2 {
        font-size: 1.7em;
        font-weight: bold;

    }

    h3 {
        font-size: 1.7em;        
    }
    
    #descriptor {
        min-width: 250px;
    }

    #phone {
        text-align: right;
        flex: 1;
    }

    #tempheader {
        position: fixed;
        top: 0;
        height: 150px;
        display: flex;                        
        align-items: center;      
        width: 100%;
        max-width: 1400px;
        background-color: white;            
    }

    #tempheader div {
        margin-left: 50px;        
    }

    #header {
        position: fixed;
        top: 150px;
        display: flex;
        height: 60px;
        width: 100%;
        max-width: 1400px;        
        background-color: white;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        box-shadow: 0px 5px 5px -4px #777;
    }

    #header-menu {        
        text-align: left;
        display: flex;
        flex-flow: row nowrap;
    }

    #cart-link {        
        text-align: right;        
    }

    #cart-link a {
        border: none;
        padding: 7px;
        border-radius: 10px;        
        background-color: #f0712c;
        color: white;
        width: 250px;       
        transition: all 0.1s ease; 
    }

    #cart-link a:hover {
        background-color: #de601b;
    }



</style>






