<template>
  <router-link class="link" :to="url">{{text}}</router-link>
</template>
<script>
  export default {
    props: ['url', 'text'],
  }
</script>
<style scoped>
  .link {
    text-align: center;
    display: inline-block;
    padding: 10px;    
    color: #777;
  }
  .link:hover {
    color: black;   
  }
  .router-link-active {
    color: #f0712c;   
  }
  
</style>




